html, body {
  font-size: 12px; }

.container {
  display: -ms-flexbox;
  display: flex;
  height: 100%; }

.slider {
  width: 200px;
  border-right: 1px solid #f2f2fe; }
  .slider > div {
    padding: 10px;
    border-bottom: 1px solid #f2f2fe;
    cursor: pointer; }
  .slider .active {
    background: #3399ff;
    color: #FFF; }

.content {
  overflow: auto;
  padding: 10px;
  border: 1px solid #F2F2F2;
  -ms-flex: 1 1;
      flex: 1 1; }

.table-tree {
  border-collapse: collapse; }
  .table-tree tbody tr:hover {
    background-color: #efefef; }
  .table-tree td {
    border-bottom: 1px solid #F2F2F2; }

.dialog {
  width: 150px;
  height: 150px;
  border: 1px solid #000;
  text-align: center;
  background: #efefef; }
